.drawingListContent {
    width: 700pt;
    margin: 20pt auto;
}

.createDrawingContainer {
    padding: 20pt 20pt 0 20pt;
    display: -webkit-flex;
    display: flex;
}

.createDrawingTextInput {
    text-align: center;
    font-size: 14pt;
    height: 40pt;
    width: 320pt;
    margin: 20pt 0 20pt 20pt;
}

.createDrawingButtons {
    margin: auto 0 auto auto;
}

.drawings {
    padding: 20pt
}

.drawingListItemContainer {
    display: -webkit-flex;
    display: flex;
    padding: 1pt;
}

.drawingListItemContainer:hover {
    background-color: #f6f6f6;
    border-style: solid;
    border-width: 1pt;
    padding: 0;
}

.drawingLabel {
    font-size: 16pt;
    font-weight: bold;
    margin: auto 20pt;
    max-width: 400pt;
}

.confirmText {
    display: inline;
    font-size: 14pt;
    font-weight: bold;
}

.topButtonContainer {
    display: -webkit-flex;
    display: flex;
}

.editDeleteButtonContainer {
    display: -webkit-flex;
    display: flex;
    margin-left: auto;
    float: right;
}
.editor {
    position: relative;
}

.editorTopContainer {
    position: absolute;
}

.editorTopBox {
    width: 100%;
    height: 100%;
}

.editorBorder {
    position: absolute;
    padding: 0;
}

.textWrapper {
    position: absolute;
}

textarea {
    resize: none;
    overflow: hidden;
    padding: 0;
    width: 100%;
    height: 100%;
}

.fixedText {
    margin: 0;
}

.imgContainer {
    position: absolute;
}

.fenceImg {
    position: relative;
    display: block;
    padding: 6pt;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
}

.logoImg {
    width: 100%;
    height: 100%;
}

.imageSelectorDropdowns {
    height: 12%;
    text-align: center;
}

.selectorList {
    padding: 3pt;
    overflow: auto;
    height: 88%;
}

.selectorList > button {
    text-align: left;
    width: 100%;
    margin: 2pt 0;
}

.centerText {
    text-align: center !important;
}
.userLoginContainer {
    margin: auto;
    margin-top: 100pt;
    width: 400pt;
    text-align: center;
    padding: 20pt;
}

.userLoginSection {
    width: 70%;
    margin: 30pt auto 10pt auto;
}

.userLoginTextInput {
    font-size: 18pt;
    text-align: center;
    width: 100%;
}


@font-face {
    font-family: 'OCR-A BT';
    font-style: normal;
    font-weight: normal;
    src: local('OCR-A BT'), url(/static/media/OCR-a___.10ab563a.woff) format('woff');
}


* {
    font-family: Arial;
    box-sizing: border-box;
}

body {
    position: relative;
}

.useBorder {
    border-style: solid;
    border-width: 1pt;
}

.limitOverflow {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.topBox {
    display: -webkit-flex;
    display: flex;
    padding: 10pt 0;
}

.userInfoContainer {
    width: 420pt;
}

.topButtonContainer {
    text-align: right;
    float: right;
    margin: auto 10pt auto auto;
    display: block;
}

.message {
    padding: 10pt;
    margin: 0;
    font-size: 16pt;
    font-weight: bold;
    text-align: center;
    color: #576475;
}


/* title */


.title28 {
    font-family: "OCR-A BT";
    font-size: 28pt;
    font-weight: bold;
    margin: 0;
    padding: 10pt;
    white-space: pre;
}

.title24 {
    font-family: "OCR-A BT";
    font-size: 24pt;
    font-weight: bold;
    margin: 0;
    padding: 10pt;
    white-space: pre;
}

.title18 {
    font-family: "OCR-A BT";
    font-size: 18pt;
    margin: 0;
    padding: 8pt 10pt;
}


/* buttons */


button {
    padding: 8pt;
    margin: 8pt;
    min-width: 80pt;
    font-size: 14pt;
    cursor: pointer;
    border: none;
}

.buttonBlack, .buttonGrey:hover {
    background-color: #02050a;
    color: white;
    border: none;
    padding: 8pt;
}

.buttonGrey, .buttonWhite:hover, select {
    background-color: lightgray;
    color: black;
    border: none;
    padding: 8pt;
}

.buttonWhite, .buttonBlack:hover {
    background-color: snow;
    color: black;
    border-style: solid;
    border-width: 1pt;
    padding: 7pt;
}

select {
    font-size: 14pt;
    font-weight: normal;
    padding: 8pt;
    margin: 8pt;
}

.selectLabel {
    display: inline-block;
    font-size: 14pt;
    margin-left: 10pt;
}

button:focus, input:focus, select:focus {
    outline:none;
}

.confirmDeleteContainer {
    display: inline-block;
}
